import React from "react";
import {Button, Form, Table} from "react-bootstrap"

function Text({name, value}) {
    let v = value?.toFixed(2)

    return (
        <div className="d-flex w-100 border-top">
            <label className="font-weight-bolder mr-4">{name + ":"}</label>
            <span className="ml-auto">{v}</span>
        </div>
    )
}

function CalcResult({params}) {

    return (
        <div className="p-2 mt-2 mb-2 ml-1 mr-1 h-100 border w-auto">
            <h5> </h5>

            <Text name={"  , "} value={params?.interval}/>
            <Text name={"  , ./"} value={params?.demandInDay}/>
            <Text name={"    , ."} value={params?.demandInAll}/>
            <Text name={"    , ."} value={params?.maxDemand}/>
            <Text name={" , ."} value={params?.garant}/>
            <Text name={"  , ."} value={params?.porog}/>
            <Text name={"  , ."} value={params?.maxWant}/>
        </div>
    )
}


export default CalcResult
