import React, {useState} from "react";
import {Button, Form} from "react-bootstrap"

function Input({name, value, onChange}) {

    return (
        <Form.Group>
            <Form.Label>{name}</Form.Label>
            <Form.Control value={value} onChange={e => onChange?.(e.target.value)} type="text" placeholder={" "}/>
        </Form.Group>
    )
}

function Params({onChange}) {

    const [params, setParams] = useState({})

    function handleChange(field) {
        return value => {
            let d = {...params}
            if (!isNaN(parseFloat(value))) {
                value = parseFloat(value)
            }
            d[field] = value
            setParams(d)
            onChange?.(d)
        }
    }

    return (
        <div className="p-2 m-2 border w-100 col-3">
            <h5> </h5>
            <Form>
                <Input value={params?.demand} onChange={handleChange("demand")} name=" , ."/>
                <Input value={params?.optZak} onChange={handleChange("optZak")} name="  , ."/>
                <Input value={params?.leadTime} onChange={handleChange("leadTime")} name="  , ."/>
                <Input value={params?.delay} onChange={handleChange("delay")} name="  , ."/>
                <Input value={params?.jobDays} onChange={handleChange("jobDays")} name="  , ."/>
            </Form>
        </div>
    )
}

export default Params;
